@import url(https://fonts.googleapis.com/css2?family=Train+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.circle {
  border-radius: 50%;
  height: 100px;
  width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: black;
  margin: 5px;
  border: solid 2px rgb(48, 48, 48);
  cursor: pointer;
}
.circle:hover {
  font-size: 1.1rem;
  border: solid 4px rgb(48, 48, 48);
  font-weight: bold;
}
.green {
  background-color: green;
}
.brown {
  background-color: brown;
}
.orange {
  background-color: orange;
}
.blue {
  background-color: blue;
}
.active {
  background-image: url(../../static/media/devil.2f027cb0.png);
  box-shadow: 0 0 20px red;
  background-size: cover;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.gameover {
  background-color: peachpuff;
  padding: 1rem;
  width: 140%;
  min-height: 25%;
  border-radius: 5px;
  position: relative;
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

button {
  border-radius: 5px;
  padding: 1.5rem;
  background-image: radial-gradient(
    rgba(228, 226, 226, 0.616),
    rgb(185, 180, 180)
  );
  cursor: pointer;
  margin: 2rem;
  font-family: "Train One", cursive;
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Train One", cursive;
  background-color: peachpuff;
  text-align: center;
  line-height: 1.6;
}
h1 {
  margin: 2rem;
}

button {
  border-radius: 5px;
  padding: 1.5rem;
  background-image: radial-gradient(
    rgba(228, 226, 226, 0.616),
    rgb(185, 180, 180)
  );
  cursor: pointer;
  margin: 2rem;
  font-family: "Train One", cursive;
  -webkit-transition: all 0.5s;
}
button:hover {
  box-shadow: 0 0 50px #bada55;
  -webkit-transform: scale(1.2);
  border-radius: 10px 100px 100px 100px;
  transition-duration: 0.5s;
}
.circles {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.controls {
  margin: 2rem;
}
#score {
  margin: 2rem;
}

